<template v-if="isLoading"><loading-cue :loading-text="appPhase"></loading-cue></template><template v-else><template v-if="isError"><notification :notification-message="appError"></notification></template><template v-else><div class="salat-next"><salat :salat="nextSalat" disp-type="next"></salat><current-location :location="location"></current-location></div><div class="salat-schedule"><div class="salat-schedule__heading"><DayDisplay :dateObj="dateToDisplay"></DayDisplay><div class="salat-schedule__switcher"><span class="salat-schedule__switch" :class="{'salat-schedule__switch--inactive': selectedDay === 'today'}" @click="selectDay('today')">&larr;</span>&emsp;<span class="salat-schedule__switch" :class="{'salat-schedule__switch--inactive': selectedDay !== 'today'}" @click="selectDay('tomorrow')">&rarr;</span></div></div><salat-list :salat-list="selectedDay === 'today' ? todaySalat : tomorrowSalat"></salat-list></div></template></template>